import { defineStore } from 'pinia';
import Web3 from 'web3';
import { pool_abi, technology_abi, fee_abi,swap_abi } from '@/utils/ABI';
import { commonStore } from './common';
const pool_address = '0xF3ab2a9991Ae3e6Fa14E9125a199B19dEAD4910F';
const technology_address = '0x79F19C9b83919e95A026196a6E5Eb1b707B12314';
const fee_address = '0xAd16f2DDe1578Fa08C1e2582a958E5c67D5eC61d';
const swap_address = '0x34e98Af4D093E919302C864D150787E9A3DbA8e6';
export const setStore = defineStore('set', {
  state: () => {
    return {
      pool_methods: {},
      technology_methods: {},
      fee_methods: {},
      swap_methods: {},
      setEmpower: false,
    };
  },
  actions: {
    async getSetMethods() {
      const common_store = commonStore();
      const walletObj =
        common_store.walletType == 'BinanceChain'
          ? window.BinanceChain
          : window.ethereum;
      const web3 = new Web3(walletObj);
      const poolContract = new web3.eth.Contract(pool_abi, pool_address);
      this.pool_methods = poolContract.methods;
      const feeContract = new web3.eth.Contract(fee_abi, fee_address);
      this.fee_methods = feeContract.methods;
      const technologyContract = new web3.eth.Contract(
        technology_abi,
        technology_address
      );
      this.technology_methods = technologyContract.methods;
      const swapContract = new web3.eth.Contract(swap_abi, swap_address);
      this.swap_methods = swapContract.methods;
      this.setEmpower = true;
    },
  },
});
